//
import { Link, useNavigate } from "react-router-dom";

import {
    Button,
    EmailInput,
    Input,
    PasswordInput,
} from "@ya.praktikum/react-developer-burger-ui-components";

import {
    TEXT_DEFAULT,
    TEXT_INACTIVE_COLOR,
    TEXT_MEDIUM,
} from "../../utils/constants";

import styles from "./Register.module.css";
import { useForm } from "../../hooks/useForm";
import { registerUser } from "../../services/thunk/userThunk";
import { useAppDispatch } from "../../hooks/hooks";

const Register = () => {
    //   
    const { values, handleChange } = useForm({
        name: "",
        email: "",
        password: "",
    });

    const dispatch = useAppDispatch();
    const navigate = useNavigate();

    const onSubmitForm = async (e: React.FormEvent) => {
        e.preventDefault();
        const success = await dispatch(
            registerUser(values.name!, values.email!, values.password!)
        );
        if (success!) {
            navigate("/", { replace: true });
        }
    };

    return (
        <section className={styles.registerContainer}>
            <form onSubmit={onSubmitForm}>
                <div className={`${styles.registerHeader} ${TEXT_MEDIUM}`}>
                    
                </div>

                <Input
                    name="name"
                    value={values.name!}
                    onChange={handleChange}
                    type={"text"}
                    placeholder={""}
                    extraClass="mt-6"
                />
                <EmailInput
                    name="email"
                    value={values.email!}
                    onChange={handleChange}
                    extraClass="mt-6"
                />
                <PasswordInput
                    name="password"
                    value={values.password!}
                    onChange={handleChange}
                    extraClass="mt-6"
                />
                <div className={styles.buttonContainer}>
                    <Button htmlType="submit" type="primary" size="large">
                        
                    </Button>
                </div>
            </form>

            <div className={styles.registerFooter}>
                <p className={`${TEXT_DEFAULT} ${TEXT_INACTIVE_COLOR}`}>
                     ?{" "}
                    <Link className={styles.navigate} to="/login">
                        
                    </Link>
                </p>
            </div>
        </section>
    );
};

export default Register;
