import { useState } from "react";

import IngredientsList from "./ingredients-list/IngredientsList";
import Tabs from "./tabs/Tabs";

import { TEXT_LARGE } from "../../../utils/constants";
import { useAppSelector } from "../../../hooks/hooks";

const BurgerIngredients = () => {
    const [current, seturrent] = useState("one");

    //     (ingredientsData, isLoading, hasError)
    const { ingredients } = useAppSelector((state) => ({
        ingredients: state.ingredients,
    }));

    return (
        <section>
            <h2 className={`${TEXT_LARGE} mt-10 mb-5`}> </h2>
            <Tabs current={current} onClick={seturrent} />
            {ingredients.ingredientsData ? (
                <IngredientsList setCurrent={seturrent} />
            ) : (
                <div>
                       , 
                    
                </div>
            )}
        </section>
    );
};

export default BurgerIngredients;
